/* Desktop */
@media screen and (min-width: 835px) {
    .hamburger {
        display: none;
    }

    #drawer {
        display: none;
    }
}

/* Mobile */
@media screen and (max-width: 500px) {
    .menu {
        display: none;
    }

    .brand {
        width: 30%;
    }

    .brand h1 {
        font-size: 1.2rem;
    }

    .restaurants {
        margin: 32px 0;
        display: grid;
        grid-template-columns: repeat(1, minmax(200px, 1fr));
        grid-gap: 30px;
    }

    .restaurant-list__header__city .restaurant_city {
        font-size: 14px;
    }

    .restaurant-list .restaurant-list__content h3 a {
        font-size: 14px;
    }

    .restaurant-list .restaurant-list__content p {
        font-size: 12px;
    }

    #hamburger {
        background: none;
        border: none;
        font-size: 22px;
        text-decoration: none;
        color: black;
        width: 44px;
        height: 44px;
        cursor: pointer;
    }

    #drawer .menu-mobile {
        padding-top: 14px;
    }

    #drawer .menu-mobile ul li a {
        display: inline-flex;
        justify-content: left;
        align-items: center;
        min-width: 44px;
        min-height: 44px;
        font-size: 14px;
    }

    .hero-title {
        font-size: 1rem;
        margin: 0;
    }

    .hero-subtitle {
        font-size: 0.8rem;
        margin: 10px 0 0;
    }

    .hero-container {
        min-height: 300px;
    }

    .section-title {
        font-size: 1.3rem;
    }

