#F6F5F2;\r\n    --textColorDark: #191919;\r\n    --rating: #F9B208;\r\n}\r\n\r\nbody {\r\n    font-family: 'Rubik', sans-serif;\r\n    overflow-x: hidden;\r\n    align-items: center;\r\n    background-color: var(--bgColor);\r\n}\r\n\r\nheader {\r\n    padding: 15px 0;\r\n    position: fixed;\r\n    top: 0;\r\n    right: 0;\r\n    width: 100%;\r\n    z-index: 10;\r\n    background-color: var(--bgColor);\r\n}\r\n\r\n/* Container */\r\n.container {\r\n    max-width: 1200px;\r\n    width: 100%;\r\n    margin-left: auto;\r\n    margin-right: auto;\r\n}\r\n\r\n/* Navigation */\r\n.nav-bar {\r\n    padding: 0 16px;\r\n}\r\n\r\n\r\n.nav-content {\r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.brand {\r\n    width: 20%;\r\n}\r\n\r\n.brand h1 {\r\n    font-size: 1.3rem;\r\n    color: var(--textColorDark);\r\n}\r\n\r\n.menu {\r\n    width: 80%;\r\n}\r\n\r\n.menu ul {\r\n    list-style: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    display: flex;\r\n    justify-content: right;\r\n    gap: 20px;\r\n}\r\n\r\n.menu ul li {\r\n    display: inline;\r\n}\r\n\r\n.menu ul li a {\r\n    display: flex;\r\n    justify-content: left;\r\n    align-items: center;\r\n    border-radius: 10px;\r\n    padding: 10px;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n    font-size: 14px;\r\n    text-decoration: none;\r\n    color: var(--textColorDark);\r\n    background-color: var(--bgColor);\r\n    transition: transform 0.3s ease, background-color 0.3s ease, color 0.3s ease;\r\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.menu ul li a:hover {\r\n    background-color: rgba(0, 0, 0, 0.8);\r\n    color: var(--textColorLight);\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n.menu ul li a:active {\r\n    transform: translateY(0);\r\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n/* Mobile Navigation */\r\n#drawer {\r\n    width: 40%;\r\n    position: fixed;\r\n    height: 100%;\r\n    right: 0;\r\n    transform: translateX(100%);\r\n    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;\r\n    background-color: var(--bgColor);\r\n    z-index: 9;\r\n    opacity: 0;\r\n}\r\n\r\n#drawer.open {\r\n    opacity: 1;\r\n    transform: translateX(0);\r\n}\r\n\r\n#drawer .menu-mobile {\r\n    margin-top: 10%;\r\n}\r\n\r\n#drawer .menu-mobile ul li a {\r\n    display: inline-flex;\r\n    justify-content: left;\r\n    align-items: center;\r\n    padding-left: 10px;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n    font-size: 10px;\r\n}\r\n\r\n.menu-mobile ul {\r\n    list-style-type: none;\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.menu-mobile ul li {\r\n    padding: 10px 0;\r\n    border-bottom: 1px solid #E0E0E0;\r\n}\r\n\r\n.menu-mobile ul li:last-child {\r\n    border-bottom: none;\r\n}\r\n\r\n.menu-mobile ul li a {\r\n    display: block;\r\n    padding: 15px 16px;\r\n    text-decoration: none;\r\n    font-size: 16px;\r\n    color: var(--textColorDark);\r\n    transition: color 0.3s ease;\r\n}\r\n\r\n.menu-mobile ul li a:hover {\r\n    color: #d99c0d;\r\n}\r\n\r\n\r\n#hamburger {\r\n    background: none;\r\n    border: none;\r\n    font-size: 22px;\r\n    text-decoration: none;\r\n    color: black;\r\n    width: 44px;\r\n    height: 44px;\r\n    cursor: pointer;\r\n}\r\n\r\n/* Hero Image */\r\n.hero-container {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    margin-top: 30px;\r\n    min-height: 500px;\r\n    width: 100%;\r\n    position: relative;\r\n    text-align: center;\r\n    overflow: hidden;\r\n}\r\n\r\n.hero-image {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n    z-index: 0;\r\n}\r\n\r\n.hero-content {\r\n    position: relative;\r\n    z-index: 1;\r\n    width: 100%;\r\n    height: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    color: var(--textColorLight);\r\n    padding: 20px 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.hero-title {\r\n    font-size: 2rem;\r\n    margin: 0;\r\n}\r\n\r\n.hero-subtitle {\r\n    font-size: 1.2rem;\r\n    margin: 10px 0 0;\r\n}\r\n   \r\n/* Main Content */\r\nmain {\r\n    padding: 32px;\r\n    flex: 1;\r\n}\r\n\r\n.content {\r\n    max-width: 1200px;\r\n    width: 100%;\r\n    margin-left: auto;\r\n    margin-right: auto;\r\n}\r\n\r\n.content .content__heading {\r\n    text-align: center;\r\n    font-weight: bold;\r\n    margin: 20px 0 60px;\r\n}\r\n   \r\n/* Restaurants */\r\n.restaurants {\r\n    margin: 32px 0;\r\n    display: grid;\r\n    grid-template-columns: repeat(4, minmax(200px, 1fr));\r\n    grid-gap: 30px;\r\n}\r\n   \r\n/* Restaurant list */\r\n.restaurant-list {\r\n    width: 100%;\r\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n    background-color: var(--textColorLight);\r\n    border-radius: 8px;\r\n    position: relative;\r\n    overflow: hidden;\r\n}\r\n   \r\n.restaurant-list__header {\r\n    position: relative;\r\n    overflow: hidden;\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.restaurant-list__header__poster {\r\n    position: relative;\r\n    width: 100%;\r\n    height: 200px;\r\n    object-fit: cover;\r\n}\r\n\r\n.restaurant-list__header__city {\r\n    width: 150px;\r\n    margin-bottom: -37px;\r\n    position: relative;\r\n    top: 0;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    border-bottom-left-radius: 100px;\r\n    border-bottom-right-radius: 100px;\r\n    background-color: var(--bgCard);\r\n    padding: 5px 10px;\r\n    color: var(--textColorDark);\r\n    z-index: 2;\r\n}\r\n\r\n.restaurant-list__header__city .restaurant_city {\r\n    font-size: 16px;\r\n}\r\n  \r\n\r\n.restaurant-list .restaurant-list__header .restaurant-list__header__rating {\r\n    position: absolute;\r\n    padding: 5px 10px;\r\n    bottom: 4px;\r\n    left: 0;\r\n    border-top-right-radius: 12px;\r\n    display: inline-block;\r\n    background-color: rgba(83, 78, 78, 0.5);\r\n    color: var(--textColorLight);\r\n    backdrop-filter: blur(10px);\r\n  }\r\n  \r\n.restaurant-list .restaurant-list__header .restaurant-list__header__rating .restaurant-list__header__rating__score {\r\n    margin-left: 10px;\r\n}\r\n\r\n.restaurant-list .restaurant-list__content {\r\n    padding: 16px;\r\n    z-index: 2;\r\n}\r\n\r\n.restaurant-list .restaurant-list__content h3 {\r\n    margin: 0  0 5px;\r\n}\r\n\r\n.restaurant-list .restaurant-list__content h3 a {\r\n    color: var(--bgDark);\r\n    text-decoration: none;\r\n    position: relative;\r\n    display: inline-flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    min-width: 44px;\r\n    min-height: 44px;\r\n    padding: 10px 0 0;\r\n    font-size: 1rem;\r\n    transition: color 0.3s ease, transform 0.3s ease;\r\n}\r\n\r\n.restaurant-list .restaurant-list__content h3 a::after {\r\n    content: '';\r\n    position: absolute;\r\n    width: 100%;\r\n    transform: scaleX(0);\r\n    height: 2px;\r\n    bottom: -1px;\r\n    left: 0;\r\n    background-color: var(--bgDark);\r\n    transform-origin: bottom right;\r\n    transition: transform 0.5s ease;\r\n}\r\n\r\n.restaurant-list .restaurant-list__content h3 a:hover {\r\n    color: var(--rating);\r\n}\r\n\r\n.restaurant-list .restaurant-list__content h3 a:hover::after {\r\n    transform: scaleX(1);\r\n    transform-origin: bottom left;\r\n    background-color: var(--rating);\r\n}\r\n\r\n.restaurant-list .restaurant-list__content p {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 4;\r\n    -webkit-box-orient: vertical;\r\n}\r\n  \r\n/*Restaurants*/\r\n.restaurant {\r\n    margin: 0 auto;\r\n    width: 100%;\r\n    max-width: 800px;\r\n    display: grid;\r\n    grid-template-columns: 1fr;\r\n    gap: 18px 16px;\r\n}\r\n\r\n.restaurant .restaurant